                                   //  Í∑∏ object ÏïàÏóêÎäî ÌéòÏù¥ÏßÄ Ïù¥Îèô ÎÇ¥Ïó≠ + Ïú†Ïö©Ìïú Ìï®ÏàòÍ∞Ä Ï†ÄÏû•ÎêòÏñ¥ÏûàÏäµÎãàÎã§.
